.input {
    width: 100%;
    margin: 20px;
    padding: 7px 15px;
    color: var(--black);
    border: none;
    border-radius: 5px;
    outline-color: var(--primary);
    background: var(--white);
    box-shadow: 0 4px 4px rgb(0 0 0 / 5%);

    font-family: var(--font-family);

    font-size: 16px;
    line-height: 22px;
}

.input::placeholder {
    color: var(--gray);
}

.error {
    border: 1px solid var(--red);
}

.inputWrapper {
    position: relative;
}

.errorMessage {
    position: absolute;
    bottom: -20px;
    left: 0;

    color: var(--red);
}
