.movie-card {
    margin-bottom: 10px;
    display: flex;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s ease-in-out;
}

.movie-card:hover {
    transform: translateY(-5px);
}

.movie-content {
    flex: 1;
    padding: 20px;
}

.movie-title {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
}

.movie-details {
    margin-bottom: 15px;
}

.movie-details p {
    margin: 5px 0;
}

.movie-actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    background-color: #f0f0f0;
    border-top: 1px solid #ccc;
}

.movie-actions button {
    padding: 10px 20px;
    border: none;
    background-color: #007bff;
    color: white;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.movie-actions button:hover {
    background-color: #0056b3;
}

.movie-poster {
    width: 200px; /* Adjust width as needed */
    height: auto;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
}

@media (max-width: 765px) {
    .movie-card {
        display: flex;
        flex-direction: column;
    }
    .movie-poster {
        width: 500px;
    }
}

.star-icon {
    color: #f8ce0b;
    font-size: 20px;
    margin-right: 2px;
}